@import url("https://fonts.googleapis.com/css2?family=Edu+TAS+Beginner:wght@500;700&family=Ibarra+Real+Nova:ital@1&display=swap");
.About__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
}
@media (max-width: 900px) {
  .About__container {
    grid-template-columns: repeat(1, 1fr);
  }
}
.About__container .photo {
  overflow: hidden;
  background-image: url("./kadyrbek.jpg");
  width: 400px;
  height: 345px;
  border-radius: 42% 56% 72% 28%/42% 42% 56% 48%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  -webkit-animation: blob 3.75s linear infinite;
          animation: blob 3.75s linear infinite;
}
@-webkit-keyframes blob {
  0%, 100% {
    border-radius: 42% 56% 72% 28%/42% 42% 56% 48%;
  }
  33% {
    border-radius: 72% 28% 48% 48%/28% 28% 72% 72%;
  }
  66% {
    border-radius: 100% 56% 56% 100%/100% 100% 56% 56%;
  }
}
@keyframes blob {
  0%, 100% {
    border-radius: 42% 56% 72% 28%/42% 42% 56% 48%;
  }
  33% {
    border-radius: 72% 28% 48% 48%/28% 28% 72% 72%;
  }
  66% {
    border-radius: 100% 56% 56% 100%/100% 100% 56% 56%;
  }
}
@media (max-width: 600px) {
  .About__container .photo {
    width: 350px;
    height: 295px;
  }
}
@media (max-width: 410px) {
  .About__container .photo {
    width: 280px;
    height: 225px;
  }
}
.About__container .about {
  display: grid;
  grid-template-rows: 225px 225px;
  place-items: center;
}
.About__container .about .who {
  font-size: 30px;
  font-family: "Edu TAS Beginner", cursive;
  font-weight: bold;
}
.About__container .about div {
  font-family: "Ibarra Real Nova", serif;
}
.About__container .about div h1 {
  font-size: 30px;
}
@media (max-width: 600px) {
  .About__container .about div h1 {
    font-size: 28px;
  }
}
@media (max-width: 410px) {
  .About__container .about div h1 {
    font-size: 25;
  }
}
.About__container .about div p {
  font-size: 25px;
}
@media (max-width: 600px) {
  .About__container .about div p {
    font-size: 22px;
  }
}
@media (max-width: 410px) {
  .About__container .about div p {
    font-size: 20px;
  }
}/*# sourceMappingURL=about.css.map */